import React from "react";
// reactstrap components
import { Row, Col, UncontrolledTooltip } from "reactstrap";

import {
  MongoDB,
  Express,
  NodeJS,
  Es6,
  GitHub
} from "../../assets/logos/index.jsx";

export default function FinanceProjectCard() {
  return (
    <div
      style={{
        position: "relative",
        height: "auto",
        width: "100%",
        boxShadow: "10px 10px 93px 0px rgba(0,0,0,0.75)"
      }}
      className="bg-gradient-warning"
    >
      <div className="imgDiv">
        <img
          style={{
            height: "auto",
            width: "100%"
          }}
          src={require("../../assets/img/projects/finance.png")}
        />
      </div>
      <div className="textDiv">
        <h3 className="display-4 text-white">Budget Tracking App</h3>
        <div style={{ marginTop: "10px", borderBottom: "4px solid #FFF" }} />
        <p
          style={{
            textAlign: "justify",
            paddingTop: "10px",
            fontSize: "0.95rem",
            lineHeight: "150%",
            color: "#FFF"
          }}
        >
          This is a personal budget tracking app project. Models and APIs are
          finished for first phase. Next phase I will develop React part of the
          project. Also I will try to develop this project as first Omniplatform
          Project which will use Electron.js for Desktop, React Native for
          Mobile and React for Web.
        </p>
      </div>
      <Row className="logos justify-content-center">
        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="mongodb" target="_blank">
            <MongoDB width="50" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="mongodb">
            MongoDB
          </UncontrolledTooltip>
        </Col>
        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="es6" target="_blank">
            <Es6 width="50" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="es6">
            ECMAScript 6/7/8
          </UncontrolledTooltip>
        </Col>

        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="nodejs" target="_blank">
            <NodeJS width="50" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="nodejs">
            NodeJS
          </UncontrolledTooltip>
        </Col>
        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="express" target="_blank">
            <Express width="115" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="express">
            ExpressJS
          </UncontrolledTooltip>
        </Col>
      </Row>

      <div
        style={{
          position: "absolute",
          right: "50px",
          bottom: "-40px",
          boxSizing: "border-box",
          paddingTop: "18px",
          backgroundColor: "black",
          width: "80px",
          height: "80px",
          textAlign: "center",
          borderRadius: "50%",
          boxShadow: "10px 10px 50px 0px rgba(0,0,0,0.75)"
        }}
      >
        <a
          href="https://github.com/tunaayberk/Personal-Budget-Management-App"
          id="gitHub"
          target="_blank"
        >
          <GitHub styleR={{ width: "44px", fill: "white" }} />
        </a>
        <UncontrolledTooltip delay={0} target="gitHub">
          GitHub
        </UncontrolledTooltip>
      </div>
    </div>
  );
}
