import React from "react";
// reactstrap components
import { Row, Col, UncontrolledTooltip } from "reactstrap";

import { Css, Html, JavaScript, JQuery } from "../../assets/logos/index.jsx";

export default function PoProjectCard() {
  return (
    <div
      style={{
        position: "relative",
        height: "auto",
        width: "100%",
        boxShadow: "10px 10px 93px 0px rgba(0,0,0,0.75)"
      }}
      className="bg-gradient-warning"
    >
      <div className="imgDiv">
        <img
          style={{
            height: "auto",
            width: "100%"
          }}
          src={require("../../assets/img/projects/po.png")}
        />
      </div>
      <div className="textDiv">
        <h3 className="display-4 text-white">Publications Ontario</h3>
        <div style={{ marginTop: "10px", borderBottom: "4px solid #FFF" }} />
        <p
          style={{
            textAlign: "justify",
            paddingTop: "10px",
            fontSize: "0.95rem",
            lineHeight: "150%",
            color: "#FFF"
          }}
        >
          I worked on this project as Frontend Developer. My main task was
          Accesibility on Admin Panels.
        </p>
      </div>
      <Row className="logos justify-content-center">
        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="jquery" target="_blank">
            <JQuery width="50" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="jquery">
            JQuery
          </UncontrolledTooltip>
        </Col>

        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="javascriptLogo" target="_blank">
            <JavaScript width="50" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="javascriptLogo">
            JavaScript
          </UncontrolledTooltip>
        </Col>

        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="htmlLogo" target="_blank">
            <Html width="50" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="htmlLogo">
            HTML 5
          </UncontrolledTooltip>
        </Col>
        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="cssLogo" target="_blank">
            <Css width="50" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="cssLogo">
            CSS 3
          </UncontrolledTooltip>
        </Col>
      </Row>
    </div>
  );
}
