import React from "react";
// reactstrap components
import { Row, Col, UncontrolledTooltip } from "reactstrap";

import {
  ReactLogo,
  JavaScript,
  MaterialUi,
  Es6,
  Website,
  GitHub
} from "../../assets/logos/index.jsx";

export default function UnsplashProjectCard() {
  return (
    <div
      style={{
        position: "relative",
        height: "auto",
        width: "100%",
        boxShadow: "10px 10px 93px 0px rgba(0,0,0,0.75)"
      }}
      className="bg-gradient-warning"
    >
      <div className="imgDiv">
        <img
          style={{
            height: "auto",
            width: "100%"
          }}
          src={require("../../assets/img/projects/unsplash.png")}
        />
      </div>
      <div className="textDiv">
        <h3 className="display-4 text-white">Unsplash Image Search App</h3>
        <div style={{ marginTop: "10px", borderBottom: "4px solid #FFF" }} />
        <p
          style={{
            textAlign: "justify",
            paddingTop: "10px",
            fontSize: "0.95rem",
            lineHeight: "150%",
            color: "#FFF"
          }}
        >
          Using Unsplash API to search images. I used Reactstrap and Material Ui
          on this project. Next version, I will convert it to Electron.js App
          with different Design.
        </p>
      </div>
      <Row className="logos justify-content-center">
        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="reactLogo" target="_blank">
            <ReactLogo width="50" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="reactLogo">
            React
          </UncontrolledTooltip>
        </Col>
        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="materialui" target="_blank">
            <MaterialUi width="50" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="materialui">
            Material UI
          </UncontrolledTooltip>
        </Col>
        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="javascriptLogo" target="_blank">
            <JavaScript width="50" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="javascriptLogo">
            JavaScript
          </UncontrolledTooltip>
        </Col>
        <Col
          lg="2"
          sm="12"
          className="justify-content-center align-self-center"
          style={{ display: "flex" }}
        >
          <a href="#" id="es6" target="_blank">
            <Es6 width="50" height="50" />
          </a>
          <UncontrolledTooltip delay={0} target="es6">
            ECMAScript 6/7/8
          </UncontrolledTooltip>
        </Col>
      </Row>

      <div
        style={{
          position: "absolute",
          right: "50px",
          bottom: "-40px",
          boxSizing: "border-box",
          paddingTop: "18px",
          backgroundColor: "black",
          width: "80px",
          height: "80px",
          textAlign: "center",
          borderRadius: "50%",
          boxShadow: "10px 10px 50px 0px rgba(0,0,0,0.75)"
        }}
      >
        <a
          href="https://github.com/tunaayberk/Unsplash-Image-Search-App"
          id="gitHub"
          target="_blank"
        >
          <GitHub styleR={{ width: "44px", fill: "white" }} />
        </a>
        <UncontrolledTooltip delay={0} target="gitHub">
          GitHub
        </UncontrolledTooltip>
      </div>
      <div
        style={{
          position: "absolute",
          right: "150px",
          bottom: "-40px",
          boxSizing: "border-box",
          paddingTop: "18px",
          backgroundColor: "#9E7CC1",
          width: "80px",
          height: "80px",
          textAlign: "center",
          borderRadius: "50%",
          boxShadow: "10px 10px 50px 0px rgba(0,0,0,0.75)"
        }}
      >
        <a
          href="https://unsplash-api-image-search.herokuapp.com/"
          id="unsplash"
          target="_blank"
        >
          <Website styleR={{ width: "44px", fill: "white" }} />
        </a>
        <UncontrolledTooltip delay={0} target="unsplash">
          Live Website
        </UncontrolledTooltip>
      </div>
    </div>
  );
}
